<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <!-- <li class="cart__item product" v-for="item in $store.getters.cartDetailtProducts"
:key="item.productId"> -->
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="minusProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent="plusProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
  // вспомогательная функция
    ...mapActions(['deleteCartProduct']),
    deleteProduct(productId) {
      this.deleteCartProduct(productId);
    },
    minusProduct() {
      let productAmount;
      if (this.amount !== 0) {
        productAmount = this.amount - 1;
      } else {
        productAmount = 0;
        return productAmount;
      }
      this.amount = productAmount;
      return this.item.amount;
    },
    plusProduct() {
      this.amount += 1;
      return this.amount;
    },
  },
};
</script>
