<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <main class="content container" v-if="productLoading">
    <img src="../../public/img/svg/785.gif" alt="Loading..."
    style="position: absolute; top: 10%; left: 50%;">
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" v-if="buttonSubmit" :to="{ name: 'order' }"
          class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from './CartItem.vue';

export default {
  data() {
    return {
      productLoading: true,
      buttonSubmit: false,
    };
  },
  filters: { numberFormat },
  components: { CartItem },
  computed: {
    // вспомогательная функция
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    loadCartProducts() {
      setTimeout(() => {
        this.productLoading = false;
      }, 2000);
    },
    checkProducts() {
      if (this.products.length !== 0) {
        this.buttonSubmit = true;
      } else {
        this.buttonSubmit = false;
      }
    },
  },
  created() {
    this.loadCartProducts();
    this.checkProducts();
  },
  watch: {
    products() {
      this.checkProducts();
    },
  },
};
</script>
