<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  // Для ручного маршрутизатора
  // props: ['pageParams'],
  data() {
    return {
      productAmount: 1,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      // Для ручного маршрутизатора
      // return products.find((product) => product.id === this.pageParams.id);
      // для vue маршрутизатора +this.$route.params.id '+' для преобразования в число
      // Для локального подключения
      // return products.find((product) => product.id === +this.$route.params.id);
      return this.productData;
      // ? this.productData.map((product) => ({
      //   ...product,
      //   image: product.image.file.url,
      // }))
      // : [];
    },
    category() {
      // Для локального подключения
      // return categories.find((category) => category.id === this.product.categoryId);
      return this.productData.category;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    minusProduct() {
      let productAmount;
      if (this.productAmount !== 0) {
        productAmount = this.productAmount - 1;
      } else {
        productAmount = 0;
        return productAmount;
      }
      this.productAmount = productAmount;
      return this.productAmount;
    },
    plusProduct() {
      this.productAmount += 1;
      return this.productAmount;
    },
    productLoad() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
          .then((response) => { this.productData = response.data; })
          .catch(() => { this.productLoadingFailed = true; })
          .then(() => { this.productLoading = false; });
      }, 3000);
    },
  },
  // created() {
  //   this.productLoad();
  // },
  // Можно сократить запись таким образом. Также этот метод будет отслеживать изменения в url строке
  watch: {
    '$route.params.id': {
      handler() {
        this.productLoad();
      },
      immediate: true,
    },
  },
};
</script>
