<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductsFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :filter-colors.sync="filterColors" />

      <section class="catalog">

        <div v-if="productsLoading"><img src="../../public/img/svg/785.gif"
          alt="Preloader.gif"
          style="position: absolute; top: 60%; left: 55%; width: 10%; height: 10%;"></div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
          <button @click.prevent="loadProducts">Попробовать еще раз</button></div>

        <!-- передача из дочернего в родительский компонент -->
        <!-- <ProductList :products="products"
        @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" /> -->
        <ProductList :products="products" />

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

      </section>

    </div>
  </main>
</template>

<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductsFilter from '@/components/ProductsFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductsFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColors: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    // Для локальных данных
    // filteredProducts() {
    //   let filteredProducts = products;

    //  if (this.filterPriceFrom > 0) {
    //  eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter((product) => product.price > this.filterPriceFrom);
    //   }

    //   if (this.filterPriceTo > 0) {
    //    eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter((product) => product.price < this.filterPriceTo);
    //   }

    //   if (this.filterCategoryId) {
    //   eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter((product) => product.categoryId === this.filterCategoryId);
    //   }

    //   if (this.filterColors) {
    //    eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter((product) => product.color === this.filterColors);
    //   }

    //   return filteredProducts;
    // },

    // Для локальных данных
    // products() {
    //   const offset = (this.page - 1) * this.productsPerPage;
    //   return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    // },
    // ПОЛУЧЕНИЕ ДАННЫХ ИЗ СЕРВЕРА
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      // Для локального дуступа
      // return this.filteredProducts.length;
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColors,
            },
          })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 3000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColors() {
      this.loadProducts();
    },
  },
  // срабатывание при запуске
  created() {
    this.loadProducts();
  },
};
</script>
