<template>
  <router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
          <svg width="30" height="21" fill="currentColor">
            <use xlink:href="#icon-cart"></use>
          </svg>
          <span class="header__count" aria-label="Количество товаров"
          v-if="indicatorLoading"><img src="../../public/img/svg/785.gif" alt="Loading..."
          style="position: absolute; top: 0; left: 0;"></span>
          <span class="header__count" aria-label="Количество товаров"
          v-else>{{ $store.state.cartProducts.length }}</span>
        </router-link>
</template>

<script>
export default {
  data() {
    return {
      indicatorLoading: true,
    };
  },
  methods: {
    loadIndicator() {
      setTimeout(() => {
        this.indicatorLoading = false;
      }, 2000);
    },
  },
  created() {
    this.loadIndicator();
  },
};
</script>
